    /**
     * Function to build
     */
    private buildSpaceLongQuery(dailyCount: boolean = false): Observable<any> {
        return combineLatest(
            this._long_poll,
            this._zone,
            this._dateTz
        ).pipe(
            filter((i) => !!i[0]),
            debounceTime(500),
            switchMap((props) => {
                const type = props[0];
                const fzone = dailyCount ? this._org.building.id : props[1];
                const dateTz = props[2];
                if (!fzone) {
                    return of([]);
                }
                this._loading.next(true);
                return this.querySpace(type, fzone, dateTz, dailyCount);
            }),
            catchError((e) => { 
                return of([]);
             })
        );
    }

    private querySpace(type: string, fzone: string, dateTz: DateTZ, dailyCount: boolean = false) : Observable<Space[]> | Observable<any>{
        const start = () => {
            const s = dateTz;
            if (type === 'week') {
                return s.startOfValue('week');
            } else {
                return s.startOfValue('month');
            }
        };

        const end = () => {
            const e = start();
            if (type === 'week') {
                /**
                 *  To Do: date-tz.class.ts endOfValue subtracts 1 day from end of week for 7 days a week type which returns Friday instead of Saturday
                 *  below is the adjustment -
                 */
                return e.addValue({ days: 1 }).endOfValue('week');
            } else {
                return e.endOfValue('month');
            }
            /**
             * R--- when testing March 2022 London offices, the month is an hour short.
             * Is this a Luxon bug or London DST?
             */
        };

        // dates here are ok
        return this._spaces.queryBooking({
            zone_ids: fzone,
            available_from: start().seconds,
            available_to: end().seconds,
            ...(dailyCount) ? { daily_count: true } : ''
        });
    }


    public getDailyMeetingCount() : void {
        this._long_poll.next('month');
        this.buildSpaceLongQuery(true).subscribe((counts) => {
            this._noOfMeetings.next(counts);
            this._loading.next(false);
        });
    }

