    /**
     * Add booking to bookings listing
     * @param booking
     */
    public add(booking: Booking) {
        const bookings = this._bookings.getValue();
        const new_bookings = bookings.concat([booking]);
        this._bookings.next(new_bookings);
    }

    /**
     * Update booking in the bookings list
     * @param booking
     */
    public replace(booking: Booking) {
        const bookings = this._bookings.getValue();
        const new_bookings = bookings
            .filter(
                (bkn) =>
                    bkn.icaluid !== booking.icaluid && bkn.id !== booking.id
            )
            .concat([booking]);
        this._bookings.next(new_bookings);
    }

    /**
     * Remove booking in the bookings list
     * @param booking
     */
    public remove(booking: Booking) {
        const bookings = this._bookings.getValue();
        const new_bookings = bookings.filter(
            (bkn) => bkn.icaluid !== booking.icaluid
        );
        this._bookings.next(new_bookings);
    }

    public attentToDelete(
        booking: Booking,
        action: 'series' | 'booking' = 'booking',
        undo: boolean = false
    ) {
        const bookings = this._bookings.getValue();
        const attempted_to_delete = ((action) => {
            return (bkg: Booking) => {
                switch (action) {
                    case 'booking': {
                        const booking_master = bookings.find(
                            (bkn) => bkn.icaluid === booking.icaluid
                        );
                        if (!booking_master) return false;
                        return bkg.id === booking.id;
                    }
                    case 'series': {
                        const booking_master = bookings.find(
                            (bkg) =>
                                bkg.id ===
                                (booking.recurrence_type === 'occurrence'
                                    ? booking.recurrence_master_id
                                    : booking.id)
                        );
                        if (!booking_master) return false;
                        return (
                            booking_master.id === bkg.id ||
                            bkg.recurrence_master_id === booking_master.id
                        );
                    }
                    default:
                        false;
                }
            };
        })(action);

        const new_bookings = [...bookings].map((bkg) => {
            if (!undo && attempted_to_delete(bkg)) {
                bkg.attempted_to_delete = action;
            }

            if (undo && attempted_to_delete(bkg)) {
                bkg.attempted_to_delete = null;
            }

            return bkg;
        });

        this._bookings.next(new_bookings);
    }

    private processBookings(
        space_list: Space[],
        period: 'day' | 'week' | 'month' = 'day'
    ) {
        const start = () => {
            switch (period) {
                case 'month':
                    return this.booking_date.startOfValue('month');
                case 'week':
                    return this.booking_date.startOfValue('week');
                default:
                    return this.booking_date.startOfValue('day');
            }
        };
        const end = () => {
            const s = start();
            switch (period) {
                case 'month':
                    return s.endOfValue('month');
                case 'week':
                    return s.endOfValue('week');
                default:
                    return s.endOfValue('day');
            }
        };

        let bookings = this._bookings.getValue();
        space_list.forEach((space) => {
            return (bookings = replaceBookings(
                bookings,
                space.bookings.map((bkn) => new Booking(bkn)),
                {
                    room_email: space.room_email,
                    fromTz: start(),
                    toTz: end(),
                }
            ));
        });
        this._bookings.next(bookings);
    }

