    private filterBookings(startTz: DateTZ, endTz: DateTZ) {
        const filters = this._filters.getValue();
        const bookings = this._bookings.getValue();
        const fzone = this._zone.getValue();
        return bookings.filter((bkn) => {
            const intersects = timePeriodsIntersect(
                startTz.ms,
                endTz.ms,
                bkn.startDateTz.ms,
                bkn.endDateTz.ms
            );
            // Check if any room in the booking satisfies the conditions
            const roomSatisfiesConditions = bkn.room.some((room) => {
                const in_zone = room.zones.includes(fzone);
                const has_space =
                    !filters.space_emails?.length ||
                    filters.space_emails.includes(room.email);
                const in_zones =
                    !filters.zone_ids?.length ||
                    !!room.zones.find((zone) => filters.zone_ids.includes(zone));
                return in_zone && has_space && in_zones;
            });
    
            const type = bkn.declined ? 'declined' : bkn.getType();
    
            const show =
                !filters.hide_type?.length ||
                !filters.hide_type.includes(type as any);
            return intersects && roomSatisfiesConditions && show;
        });
    }  

    /**If Sunday add one day to get Saturday as end of week */
    private getEndOfWeek(date: DateTZ): DateTZ {
        return date.dateWeekday === 7
            ? date.addValue({ days: 1 }).endOfValue('week').endOfValue('day')
            : date.endOfValue('week').endOfValue('day');
    }
